import React from 'react';
import Button from '@material-ui/core/Button';
import { properties } from '../Properties/Properties.js';
import { TextField } from '@material-ui/core';

export default function SendFeedbackComponent(props){
    
  const [feedBack, setFeedBack] = React.useState("")
  const [sent, setSent] = React.useState(false)

  const handleFeedback = e => {
    if (feedBack !== ""){
      fetch(`${properties.address}/sendFeedback`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({text : feedBack}),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
          if (response.status === 200){
                setSent(true)
                setFeedBack("")
          }
      })
    }
  }

    return (<>
        {!sent && <TextField multiline onBlur = { e => setFeedBack(e.target.value)}/>}
        <Button onClick = {sent === false ? handleFeedback : ()=>{} }>{sent === false ? "" : ""}</Button>
    </>)
}