import React from 'react';
import { useParams } from 'react-router-dom'

import CircularProgress from '@material-ui/core/CircularProgress'
import { properties } from '../../Properties/Properties.js';
import BaseSurveyComponent from "../BaseSurveyComponent";
import MapChart from '../MapChart.js'
import { PopupContext } from '../../index'

export default function SeparateSurveyComponent(props) {
    const { id: surveyId } = useParams()
    const [survey, setSurvey] = React.useState()
    const setMessageFunc = React.useContext(PopupContext)
    React.useEffect(() => {
        fetch(`${properties.address}/survey?id=${surveyId}`,
            {
                method: 'GET',
                credentials: 'include',
            })
            .then(response => {
                if (response.status === 200)
                    return response.json()
            })
            .then(result => {
                if (result)
                    setSurvey(result)
            })
    }, [])
    return <>
        {!survey && <CircularProgress />}
        {survey && <BaseSurveyComponent surveyJson={survey} />}

        <div style={{ width: "500px", height: "200px", border: "1px solid black" }}>
            <MapChart markers={survey && survey.locations} />
        </div>
    </>
}