import React from 'react';
// import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent'

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';
import Checkbox from '@material-ui/core/Checkbox'
import { properties } from '../Properties/Properties.js'

const useStyles = makeStyles((theme) => ({
  list: {
    padding: 0
  },
  checkbox: {
    padding: 3
  },
  tag: {
    margin: 2,
    backgroundColor: "#a2a2dc",
    margin: 2,
    borderRadius: 5,
    cursor: "pointer",
    '&:hover': {
      backgroundColor: "#c3c3ec",
    },
    fontSize: 10,
    color: "#2f4f4f",
    padding: 2,
    width: "max-content"
  },
}))
export default function CategoriesComponent(props) {
  const classes = useStyles()
  const [state, setState] = React.useState({
    categories: [],
    checked: [0],
  })

  const [categories, setCategories] = React.useState([])
  const [checked, setChecked] = React.useState([0])

  React.useEffect(() => {
    fetch(`${properties.address}/getCategories`,
      {
        method: 'GET',
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200)
          return response.json()
      })
      .then(result => {
        setChecked(result.filter(category => category.checked === true)
          .map(category => category.name))
        setCategories(result)
      })

    // fetch(`${properties.address}/getAllTags`,
    //   {
    //     method: 'GET',
    //     redirect: 'follow',
    //     credentials: 'include',
    //   })
    //   .then(response => {
    //     if (response.status === 200) {
    //       return response.json()
    //     }
    //   })
    //   .then(result => {
    //     setTags(result)
    //   })
  }, [])


  const handleToggle = (value) => () => {
    const currentIndex = checked.indexOf(value);
    const checkBoxArr = [...checked];

    if (currentIndex === -1)
      checkBoxArr.push(value)
    else
      checkBoxArr.splice(currentIndex, 1)


    let body = {
      "skipped": categories
        .filter(category => !checkBoxArr.includes(category.name))
        .map(category => category.name)
    }

    fetch(`${properties.address}/updateChecked`,
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(body),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          // props.Load(undefined, "")
          setChecked(checkBoxArr)
        }
      })


  }

  return <>
    <Card>
      <CardContent>
        <List component="nav">
          <ListItem className={classes.list}>
            <ListItemText primary="" />
          </ListItem>
          <Divider />
          {categories.map(category => {
            const labelId = `checkbox-list-label-${category.name}`
            return (
              <ListItem
                className={classes.list}
                key={`category-${category.id}`}
                button
                onClick={handleToggle(category.name)}>
                <ListItemIcon>
                  <Checkbox
                    className={classes.checkbox}
                    edge="start"
                    checked={checked.indexOf(category.name) !== -1}
                    tabIndex={-1}
                    disableRipple
                    inputProps={{ 'aria-labelledby': labelId }}
                  />
                </ListItemIcon>
                {/* <ListItemIcon>
                    {category.icon}
               </ListItemIcon> */}
                <ListItemText primary={category.name} />
              </ListItem>)
          }
          )}
        </List>
      </CardContent>
    </Card>
    {/* <br></br> */}
    {/* <Card style={{ height: 150, overflowY: "auto" }}>
      <CardContent >
        <List component="nav" aria-label="main mailbox folders">
          <ListItem className={classes.list}>
            <ListItemText primary="" />
          </ListItem>
          <Divider />
        </List>
        <div style={{ alignContent: "center" }}>
          {tags.map(tag =>
            <Button
              onClick={e => handleTag(tag.name)}
              className={classes.tag}
              color="primary"
              variant="contained"
              key={tag.id}
            >
              {tag.name}
            </Button>
            // <Button  color = "primary" variant = "contained" key = {tag.id}>{tag.name}</Button>
          )}
        </div>

      </CardContent>

    </Card> */}
    {/* <Popper open={open} anchorEl={anchorEl} placement="top" transition>
      <Paper style={{ padding: 10 }}>
        <Typography variant="caption" className={classes.typography} style={{ width: 200 }}>
              
          </Typography>
      </Paper>
    </Popper> */}
  </>
}