import React, { Children, cloneElement } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import FavoriteIcon from '@material-ui/icons/Favorite';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import CheckIcon from '@material-ui/icons/Check';
import Tooltip from '@material-ui/core/Tooltip';
import LoopIcon from '@material-ui/icons/Loop';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ThumbUpAltIcon from '@material-ui/icons/ThumbUpAlt';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import DeleteOutlineOutlinedIcon from '@material-ui/icons/DeleteOutlineOutlined';
import { properties } from '../Properties/Properties.js';
import { Link } from 'react-router-dom';
import { UserContext } from '../index'

const useStyles = makeStyles((theme) => ({
  header: {
    padding: 0,
    backgroundColor: theme.palette.primary.main,
  },
  expand: {
    transform: 'rotate(0deg)',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: "#41465f",
  },
  tag: {
    margin: 2,
    backgroundColor: "#a2a2dc",
    margin: 2,
    borderRadius: 5,
    cursor: "pointer",
    '&:hover': {
      backgroundColor: "#c3c3ec",
    },
    fontSize: 15,
    color: "#2f4f4f",
    paddingLeft: 5,
    paddingRight: 5,
    fontFamily: theme.typography.fontFamily
  },
  action: {
    margin: 0
  }
}));

export default function CardComponent(props) {
  const userInfo = React.useContext(UserContext)
  const classes = useStyles()
  const [state, setState] = React.useState({
    answered: props.surveyJson.answered,
    answeredAmount: props.surveyJson.answered_amount,
    likes: props.surveyJson.likes,
    voted: props.surveyJson.voted,
    flipped: true,
    anchorEl: null,
    expanded: false,
    commentError: "",
    comment: "",
    comments: props.surveyJson.comments,
  })

  const handleFlip = e => setState({ ...state, flipped: !state.flipped })
  const handleExpandClick = () => setState({ ...state, expanded: !state.expanded })
  const handleClick = e => setState({ ...state, anchorEl: e.currentTarget })
  const handleClose = e => setState({ ...state, anchorEl: null })

  const handleLike = e => {
    setState({ ...state, voted: !state.voted })
    fetch(`${properties.address}/like`,
      {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({ surveyId: props.surveyJson.id })
      })
      .then(response => {
        if (response.status === 403) {
          console.log("403")
          setState({ ...state, voted: props.surveyJson.voted })
        }
        return response.json()
      })
      .then(json => {
        if (json.result === 'Added') {
          setState({ ...state, likes: json.likes, voted: true })
        }
        else if (json.result === 'Deleted') {
          setState({ ...state, likes: json.likes, voted: false })
        }
      })
  }

  const handleDelete = e => {
    fetch(`${properties.address}/deleteSurvey`,
      {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ surveyId: props.surveyJson.id }),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200)
          props.onSurveyDeleteFunc()
      })
  }
  const handleCommentSubmit = e => {
    e.preventDefault()

    if (state.comment.length < 5 || state.comment.length > 200) {
      setState({ ...state, commentError: "  5    200 " })
      return
    }
    setState({ ...state, commentError: "" })

    fetch(`${properties.address}/addComment`,
      {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({
          content: state.comment,
          surveyId: props.surveyJson.id
        })
      })
      .then(response => {
        if (response.status == 200)
          return response.json()
      })
      .then(result => {
        if (!result)
          return
        console.log(result)
        let newComments = state.comments
        let date = new Date(result.date)
        newComments.push({
          "content": state.comment,
          "likes": 0,
          "user_id": result.user_id,
          "username": "admin",
          "date": `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`,
          "id": result.id,
        })

        setState({ ...state, comments: newComments })
      })

  }

  const handleCommentLike = comment => {
    comment.likes = comment.voted ? comment.likes - 1 : comment.likes + 1
    comment.voted = !comment.voted
    setState({ ...state, comments: state.comments })

    fetch(`${properties.address}/commentLike`,
      {
        headers: {
          'Content-Type': 'application/json'
        },
        method: 'POST',
        credentials: 'include',
        body: JSON.stringify({ commentId: comment.id })
      })
      .then(response => {
        if (response.status == 200) {
          return response.json()
        }
      }).then(result => {
        if (result) {
          // new_comments = comments.find(comment -> comment.id === commentId)
          comment.likes = result.likes
          comment.voted = result.voted
          setState({ ...state, comments: state.comments })
        }
      })
  }

  const handleDeleteComment = commentId => {
    fetch(`${properties.address}/deleteComment`,
      {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          commentId: commentId
        }),
        redirect: 'follow',
        credentials: 'include',
      })
      .then(response => {
        if (response.status === 200) {
          let newComments = state.comments.filter(comment => comment.id !== commentId)
          setState({ ...state, comments: newComments })
        }
      })
  }

  return <>
    <Menu
      id="simple-menu"
      anchorEl={state.anchorEl}
      keepMounted
      open={Boolean(state.anchorEl)}
      onClose={handleClose}
    >
      <MenuItem onClick={() => { handleClose(); handleDelete(); }} ></MenuItem>
    </Menu>
    <Card>
      <CardHeader className={classes.header}
        avatar={<Avatar variant="square" className={classes.avatar} />}

        action={userInfo.userId === props.surveyJson.userId ? <IconButton onClick={handleClick} size={properties.iconSize} edge="start"><MoreVertIcon /></IconButton> : ""}

        subheader={<Link
          to={`/survey/${props.surveyJson.id}`}
          style={{ textDecoration: 'none' }}>
          {props.surveyJson.date}</Link>}
        classes={{ action: classes.action }}
      />
      <CardContent style={{ padding: "8" }}>
        {/* {Children.map(props.children, child => cloneElement(child, { flipped: state.flipped, votedCallback: votedCallback }))} */}
        {Children.map(props.children, child => cloneElement(child, { flipped: state.flipped }))}
      </CardContent>
      <CardActions disableSpacing>
        <Tooltip title={props.Answered ? ' ' : '  '}>
          <IconButton size={properties.iconSize} className={classes.icon}>
            <CheckIcon color={props.Answered ? 'secondary' : 'primary'} />
          </IconButton>
        </Tooltip>
        {state.answeredAmount > 0 && state.answeredAmount}
        <IconButton size={properties.iconSize} className={classes.icon} onClick={handleLike}>
          <FavoriteIcon color={state.voted ? 'secondary' : 'primary'} />
        </IconButton>
        {state.likes > 0 && state.likes}
        {state.answered &&
          <IconButton size={properties.iconSize} onClick={handleFlip}> <LoopIcon /> </IconButton>
        }
        {props.surveyJson.tags.map(tag =>
          <div className={classes.tag}>{tag}</div>
        )}
        <Typography style={{ marginLeft: "auto", marginRight: 5, marginBottom: 0 }} paragraph> ({state.comments.length})</Typography>
        <IconButton
          className={clsx(classes.expand, {
            [classes.expandOpen]: state.expanded,
          })}
          onClick={handleExpandClick}
          aria-expanded={state.expanded}
        >
          <ExpandMoreIcon />
        </IconButton>
      </CardActions>





      <Collapse style={{ borderTop: "1px solid #e0e0e0" }} in={state.expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <List>
            {state.comments.map(comment => <>
              <ListItem style={{ padding: 0, paddingLeft: 10 }} alignItems="flex-start">
                <ListItemAvatar>
                  <Avatar style={{ width: 30, height: 30 }} variant="rounded" aria-label="" className={classes.avatar} >
                    {comment.username && comment.username.charAt(0)}
                  </Avatar>
                </ListItemAvatar>

                <ListItemText
                  primary={<>
                    {comment.username + "  "}
                    <Typography variant="caption" component="span" color="textSecondary">
                      {comment.date}
                    </Typography>
                  </>}
                  secondary={
                    <React.Fragment>
                      <div style={{ display: "flex", wordBreak: "break-word" }}>

                        <Typography
                          component="span"
                          variant="body2"
                          style={{ display: "inline" }}
                          color="textPrimary"
                        >
                          {comment.content}
                        </Typography>
                        <div style={{ marginLeft: "auto" }}>
                          {/* {comment.user_id === props.LoggedInUserId && <IconButton className={classes.icon} onClick={e => handleDeleteComment(comment.id)}>
                            <DeleteOutlineOutlinedIcon />
                          </IconButton>} */}
                          {<IconButton className={classes.icon} onClick={e => handleDeleteComment(comment.id)}>
                            <DeleteOutlineOutlinedIcon />
                          </IconButton>}
                          <Typography component="span">
                            {comment.likes != 0 && comment.likes}
                          </Typography>

                          <IconButton color={comment.voted ? "secondary" : "primary"} className={classes.icon} aria-label="add to favorites" onClick={e => { handleCommentLike(comment) }}>
                            <ThumbUpAltIcon />
                          </IconButton>
                        </div>
                      </div>

                    </React.Fragment>
                  }
                />
              </ListItem>
              <Divider variant="inset" component="li" />
            </>)}
          </List>
          <FormControl style={{ display: "flex" }}>
            <form onSubmit={handleCommentSubmit}>
              <TextField
                style={{ display: "flex" }}
                id="standard-textarea"
                label=""
                placeholder=""
                multiline
                onBlur={e => { setState({ ...state, comment: e.target.value }); e.target.value = "" }}
              />
              {state.commentError && <FormHelperText error={true}>{state.commentError}</FormHelperText>}
              <Button onClick={handleCommentSubmit} type="submit" variant="contained" ></Button>
            </form>
          </FormControl>
        </CardContent>
      </Collapse>

    </Card>
  </>
}
