import React from 'react';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import Button from '@material-ui/core/Button';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import DefaultTooltipContent from 'recharts/lib/component/DefaultTooltipContent';

export default function SingleSurveyComponent(props) {
  props = props.props
  const [state, setState] = React.useState({
    error: false,
    voted: props.surveyJson.answered,
    choosedOption: "",
    helperText: props.HelperText,
    chart: undefined
  })


  const handleSubmit = e => {
    e.preventDefault()
    let submitOptions = {
      "surveyId": props.surveyJson.id,
      "choosedOption": state.choosedOption
    }
    props.handleSubmit(e, submitOptions, validate, processDataFunc)
  }

  const validate = () => {
    if (state.choosedOption === "") {
      console.log("choose smth")
      setState({ ...state, error: true, helperText: " " })
      return false
    }
    setState({ ...state, error: false, helperText: props.HelperText });
    return true
  }

  // Example of stats data
  // const data = [
  //   {
  //     "name": "opt1",
  //     "value": 30,
  //     "count": 54
  //   }]
  const processDataFunc = (e, result) => {
    let longestOption = result.map(elem => elem.name).sort((e1, e2) => e2.length - e1.length)[0].length
    let choosedOption = props.surveyJson.choosedOption || state.choosedOption
    let chart = <BarChart width={14 * longestOption + 200} height={40 * result.length} data={result} layout="vertical">
      <XAxis type="number" hide />
      <YAxis
        tick={props => {
          return <text
            x={props.x}
            y={props.y}
            textAnchor="end"
            fontWeight={props.payload.value === choosedOption ? "bold" : "normal"}
            fill={props.payload.value === choosedOption ? "blue" : "black"}
          >
            {props.payload.value + " (" + result.filter(r => r.name === props.payload.value)[0].value + "%)"}
          </text>
        }}
        type="category" dataKey="name" width={14 * longestOption} />
      <Tooltip content={props => {
        if (props.payload[0] != null) {
          const newPayload = [{
            name: '',
            value: props.payload[0].payload.count
          }]
          return <DefaultTooltipContent {...props} payload={newPayload} />
        }
        return <DefaultTooltipContent {...props} />
      }} />
      <Bar dataKey="value" fill="#8884d8" />
    </BarChart>
    setState({ ...state, chart: chart })
  }

  const handleChange = (event) => {
    setState({ ...state, choosedOption: event.target.value })
  }

  React.useEffect(() => {
    if (props.Answered)
      props.getStats(undefined, processDataFunc)
  }, [props.Answered])

  return <FormControl
    component="fieldset"
    error={state.error}
  >
    {/* {props.flipped &&
    <StatisticsComponent
      Options={props.surveyJson.options}
      Type="Multiple"
      SurveyId={props.surveyJson.surveyId}>
    </StatisticsComponent>} */}
    {!props.flipped && <form onSubmit={handleSubmit}>
      <FormLabel component="legend">{props.surveyJson.question}</FormLabel>
      {!props.Answered && <RadioGroup value={state.choosedOption} onChange={handleChange}>

        {props.surveyJson.options.map((option) =>
          <FormControlLabel
            value={option}
            control={<Radio />}
            label={option}
          />
        )}
      </RadioGroup>}
      {props.Answered && state.chart}
      <FormHelperText>{state.helperText}</FormHelperText>
      {!props.Answered && <Button type="submit" variant="contained" color="primary"></Button>}
    </form>}
    {/* {state.voted && !props.Flipped && 
    <StatisticsComponent 
    Options={this.props.Options} 
    Type="Single" 
    SurveyId={this.SurveyId} />} */}
  </FormControl>
}