import React from 'react'
import { properties } from '../../Properties/Properties.js'
import BaseSurveyComponent from "../BaseSurveyComponent"
import CategoriesComponent from "../CategoriesComponent"
import MapChart from '../MapChart.js'
import { PopupContext } from '../../index'

import Grid from '@material-ui/core/Grid';


export default function RandomComponent(props) {
    const setMessageFunc = React.useContext(PopupContext)
    const [state, setState] = React.useState({
        isLoading: true,
        survey: {}
    })

    const getRandom = e => {
        fetch(`${properties.address}/getRandom`, { method: 'GET', credentials: 'include' })
            .then(response => {
                if (response.status === 200)
                    return response.json()
            })
            .then(result => setState({ ...state, survey: result, isLoading: false }))
    }
    React.useEffect(() => {
        getRandom()
    }, [])
    const onSurveyDeleteFunc = e => {
        setMessageFunc(" :)")
        setState({ ...state, isLoading: true }); 
        getRandom()
    }
    return (
        <>

            <Grid container spacing={1}>
                <Grid item sm={3} >
                </Grid>
                <Grid item sm={6} >
                    {state.survey != null && !state.isLoading && <BaseSurveyComponent surveyJson={state.survey} onSurveyDeleteFunc = {onSurveyDeleteFunc} />}

                    {state.survey == null && "   :("}
                    <button onClick={() => { setState({ ...state, isLoading: true }); getRandom() }}>press me</button>
                    <div style={{ width: "500px", height: "200px", border: "1px solid black" }}>
                        <MapChart markers={state.survey != null && !state.isLoading && state.survey.locations} />
                    </div>
                </Grid>
                <Grid item sm={3} >
                    <CategoriesComponent />
                </Grid>
            </Grid>
        </>)
}